import TextField from '@mui/material/TextField';
import { useEffect, useState } from 'react';
import axios from 'axios';
import Item from '../components/item';
import React from 'react';

function SellPage({base_url}) { 
    const [response, setresponse] = useState({})
    function getInventory () {
        axios.get(`${base_url}api/inventory/`, {withCredentials: true}).then((r) => {console.log(r) 
            localStorage.setItem('inventory', JSON.stringify(r));
            setresponse(r);
        });
      }
    useEffect(() => {
        setresponse((JSON.parse(localStorage.getItem('inventory')))?.data)
        console.log(response);
        if (!response) {
            getInventory();
        }
    }, [localStorage.getItem('inventory')])
 
    const template_items = [];
    
    for (let population in response?.rgDescriptions) {
        template_items.push(response.rgDescriptions[population]);
    }
    console.log(template_items);

    const items = [];

    for (let population in response?.rgInventory) {
        items.push(response.rgInventory[population]);
    }
    console.log(items);
  return (
    <div className="sell-page">
         <div className="modal-items">
            <div className="modal-header">
               <img className="steam-icon" src="https://avan.market/templates/frontend/default/images/steam-icon-skins.svg"/>
                <span className="text-with-icon"><b>Ваш</b> инвентарь</span>
            </div>
            <div className="link">
                    <span className="input-your-link">Вставьте свою ссылку на обмен:</span>
                    <input type="text" className="input-link" placeholder="Ссылка на обмен"/>
                    <button className="save-offer-link">СОХРАНИТЬ</button>
                </div>
                <div className="items">
                            {items.map((item)=>{
                                for (let i = 0; i < template_items.length; i++) {
                                    if (item.instanceid == template_items[i].instanceid && item.classid == template_items[i].classid) {
                                        if (template_items[i].tradable && template_items[i].marketable){ 
                                            return(
                                        <Item key={Math.random()} item={template_items[i]}/>)}
                                    }
                                }
                            })}
                </div>
                <div className='selected'>Выбрано: 0
                <button className='reload-inventory' onClick={getInventory}>Обновить</button></div>
                
         </div>

         <div className="modal-items">
            <div className="modal-header">
                <img className="steam-icon" src="https://avan.market/templates/frontend/default/images/steam-icon-skins.svg"/>
                <span className="text-with-icon"><b>Вы</b> получите</span>
            </div>
            <div className='amount'>
                <span className='trade-amount'>
                    <span className='dash'><b>//</b></span>
                    <span className='cost'>0.0</span>
                    <span className='cur'>₽</span>
                </span>
            </div>
            
            <div className='method'>
               <div className='name'>Выбрать способ оплаты</div>
               <div className='payment-list'>
                     <div className='payment visa'></div>
                     <div className='payment qiwi'></div>
                     <div className='payment bitcoin'></div>
                     <div className='payment etherium'></div>
               </div>
            </div>
            <div className='fields'>
                <div className='text-total-desc'>Ваша электронная почта</div>
                <input type="text" className="email" placeholder="E-mail"/>
                <div className='text-total-desc'>Платежные реквизиты</div>
                <input type="text" className="email" placeholder="Реквизиты"/>
            </div>
            <div className='total-desc'>
                <div className=''>
                        <span>Комиссия:</span>
                        <span>0.0₽</span>
                </div>
                <div className=''>
                        <span>Итого к выплате:</span>
                        <span>0.0₽</span>
                </div>
            </div>
            <button className="get-money">ПОЛУЧИТЬ ДЕНЬГИ</button>
         </div>
        
    </div>
  );
}

export default React.memo(SellPage);