
import '../App.css';


function Item({item}) { 
    const url = 'http://steamcommunity-a.akamaihd.net/economy/image/'
   return (
    <div className='item'>
        <img className='icon' src={url + item.icon_url}/>
        <span className='item-name'>{item.market_name}</span>
    </div>
   );
}

export default Item;