import './App.css';
import axios from 'axios';
import { useEffect, useState } from 'react';
import logo from './images/NsDqWRymyZE1(1)1.png'
import background from './images/mossawi_192164419218_20170410164335_14843.png'
import image1 from './images/image1.png'
import { useNavigate } from 'react-router';
import Header from './components/header';
import Routes from './Routes';

//const BASE_URL = 'http://localhost:8000/'
const BASE_URL = 'https://csgo-site.herokuapp.com/'

function App() { 
  const [response, setResponse] = useState({});
  const [isLogin, setLogin] = useState(false);
  function login() {
    window.location.href = `${BASE_URL}accounts/steam/login/`
  }
  function logout() {
    axios.get(`${BASE_URL}accounts/logout/`, {withCredentials: true}).then(() => {
      setLogin(false);
      setResponse({});
    }).catch(() => {
      setLogin(false);
      setResponse({});
    })
    }
    function getInventory () {
      axios.get(`${BASE_URL}api/inventory/`, {withCredentials: true}).then((r) => {console.log(r)
        localStorage.setItem('inventory', JSON.stringify(r));
      });
      
    }

  useEffect(() =>{
    getInfo();
  },[])

    function getInfo() { 
    axios.get(`${BASE_URL}api/me/`, {withCredentials: true}, {withCredentials: true}).then((resp) => {
      setResponse(resp.data);
      setLogin(true);
    }).catch(() => {
      setLogin(false)})
   }

  return (
    <>
    <div className="background" style={{backgroundImage:`url(${background})`, zIndex: 100}}>
      <Header logo={logo} getInventory={getInventory} login={login} logout={logout} isLogin={isLogin} response={response}/>
      <Routes base_url={BASE_URL}/>
    </div>
    
    </>
  );
}

export default App;