import { useNavigate } from 'react-router';
import '../App.css';


function Header({logo, getInventory, login, logout, isLogin, response}) { 
    let navigate = useNavigate();
   return (
    <>
      <div className="header">
        <img className="logo" style={{cursor: 'pointer'}} onClick={()=>navigate('/')} src={logo} />
        <div className="menu">
          <span onClick={getInventory} className="text">Продать предметы</span>
          <span className="text">Отзывы</span>
          <span className="text" onClick={()=>navigate('/sell/')}>Личный кабинет</span>
        </div>
        <div className="auth">
          <img src={response?.extra_data?.avatar} className="login-btn"/>
          {!isLogin && <span className="text" onClick={login}>Войти через Steam</span>}
          {isLogin && <span className="nickname">{response?.extra_data?.realname}</span>}
          {isLogin && <span className="text" onClick={logout}>Выйти</span>}
        </div>
      </div>
    </>
  );
}

export default Header;